EthercatUnits.allow({
	insert: function (userId, doc) {
		return EthercatUnits.userCanInsert(userId, doc);
	},

	update: function (userId, doc, fields, modifier) {
		return EthercatUnits.userCanUpdate(userId, doc);
	},

	remove: function (userId, doc) {
		return EthercatUnits.userCanRemove(userId, doc);
	}
});

EthercatUnits.before.insert(function(userId, doc) {
	doc.createdAt = new Date();
	doc.createdBy = userId;
	doc.modifiedAt = doc.createdAt;
	doc.modifiedBy = doc.createdBy;

	
	if(!doc.createdBy) doc.createdBy = userId;
});

EthercatUnits.before.update(function(userId, doc, fieldNames, modifier, options) {
	modifier.$set = modifier.$set || {};
	modifier.$set.modifiedAt = new Date();
	modifier.$set.modifiedBy = userId;

	
});

EthercatUnits.before.remove(function(userId, doc) {
	
});

EthercatUnits.after.insert(function(userId, doc) {
	var message = doc.message;
	var sensorArr = [];
	for(var num in message.slaves){
		var slave = message.slaves[num];
		var sensorid = slave.name;
		for(var num2 in slave.inputs){
			var channel = slave.inputs[num2];
			var tempObj = {};
			tempObj["signal"] = sensorid+"."+channel.name+"."+channel.offsetbyte+"."+channel.offsetbit+"."+channel.bitlength;
			tempObj["value"] = null;
			tempObj["timestamp"] = null;
			tempObj["offsetbyte"] = channel.offsetbyte;
			tempObj["offsetbit"] = channel.offsetbit;
			tempObj["bitlength"] = channel.bitlength;
			sensorArr.push(tempObj);
		}
	}

	console.log(sensorArr);

	sensorArr.forEach(function(sensor,i){
		var searchSignal = Signals.findOne({"signal":sensor.signal});
		if(searchSignal !== undefined){
			var offsetA = searchSignal.offsetA;
			var offsetB = searchSignal.offsetB;
			var scale = searchSignal.scale;
			Signals.upsert({"signal":sensor.signal},{$set:{value: scale*(sensor.value+offsetA)+offsetB, raw: sensor.value, timestamp: sensor.timestamp}});
		}
		else{
			Signals.upsert({"signal":sensor.signal},{$set:{name: null,bus: "ethercat",value: sensor.value, raw: sensor.value, timestamp: sensor.timestamp, unit: unit, offsetA: 0.0, offsetB: 0.0, scale: 1.0, hidden: false, offsetbyte: sensor.offsetbyte, offsetbit: sensor.offsetbit, bitlength: sensor.bitlength}});
		}
	});
});

